<template>
  <nav class="navbar navbar-expand-lg navbar-dark bg-brown p-2 fs-5 sticky-top">
          <router-link to="/"><img src="@/assets/images/coffee_logo.png" alt="logo" style="width: 48px;"></router-link>
          <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
          </button>
          <div class="collapse navbar-collapse justify-content-end" id="navbarNavAltMarkup">
            <div class="navbar-nav fw-bold">
              
              <router-link to="/" class="nav-item nav-link ms-2 ">
                <i class="bi bi-house-fill"></i> 首頁
              </router-link>
              <router-link to="/articles" class="nav-item nav-link ms-2 ">
                <i class="bi bi-bookmark-fill"></i> 最新消息
              </router-link>
              <router-link to="/products" class="nav-item nav-link ms-2 ">
                <i class="bi bi bi-card-list"></i> 產品
              </router-link>
              <router-link to="/question" class="nav-item nav-link ms-2 ">
                <i class="bi bi-question-circle"></i> 常見問題
              </router-link>
              <router-link to="/favorite" class="nav-item nav-link ms-2 ">
                <i class="bi bi-heart-fill"></i> 收藏
              </router-link>
              <router-link to="/cart" class="nav-item nav-link ms-2 ">
                <i class="bi bi-cart-fill position-relative "></i> 購物車
                <span v-if="carts.length !== 0"
                 class="position-absolute start-lg-100 top-lg-0 translate-middle badge rounded-pill bg-danger">{{ carts.length }}</span>
              </router-link>
            </div>
          </div>
        </nav>
</template>
<script>
import emitter from '@/methods/emitter';
export default {
  data() {
    return {
      carts:[],
    }
  },
  methods: {
    getCart() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;
      this.isLoading = true;
      this.$http.get(url).then((res) => {
        this.carts = res.data.data.carts;
      });
    },
  },
  created() {
    // this.getCart();
    emitter.on("get-cart", () => {
      this.getCart();
    });
  }
}
</script>
